@import "../../Style.scss";


.post{
    @include themify($themes){
        box-shadow: 0px 0px 25px -10px rgba(0,0,0,0.38);
       -webkit-box-shadow: 0px 0px 25px -1px rgba(0,0,0,0.38);
       -moz-box-shadow: 0px 0px 25px -10px rgba(0,0,0,0.38);
       border-radius: 20px;
       background-color: themed("bg");
       color: themed("textColor");
       

    .container{
        padding: 20px;
        .user{
            display: flex;
            align-items: center;
            justify-content: space-between;
            .userInfo{
                gap: 20px;
                display: flex;
                img{
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                    gap: 20px;
                }
                .details{
                    display: flex;
                    flex-direction: column;

                    .name{
                        font-weight: 500;

                    }
                    .date{font-size: 12px;
                    }
                }
            }
        }
      
        .content{
            
            padding-left: -50px;

            img{
                width: 100%;
                max-height: 500px;
                object-fit: cover;
                border-radius: 10px;
            }
            
        }
        .info{
            display: flex;
            align-items: center;
            gap: 20px;
            .items{
                display: flex;
                align-items: center;
                gap: 10px;
                cursor: pointer;
                font-size: 14px;
            }
        }
    }
}
}