.register{
    display: flex;
    height: 100vh;
    background-color: rgb(193, 190, 190, 255);
    align-items: center;
    justify-content: center;
    
    .card{
    display: flex;
    background-color: white;
    width: 50%;
    border-radius: 10px;
    background-color: white;
    border-radius: 10px;
    min-height: 500px;
     overflow: hidden;
     flex-direction: row-reverse;
    
    .left{
    flex: 1;
    background: linear-gradient(rgba(39, 11, 96, 0.5), rgba(39, 11, 96, 0.5)), url('https://images.pexels.com/photos/1266810/pexels-photo-1266810.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1') center;
    background-size: cover;
    padding: 50px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: white;
    
     h1{
    font-size: 70px;
    line-height: 80px;
    }
     p{
    color: white;
     }
     span{
    font-size: 14px;
    }
    button{
    width: 50%;
    color: purple;
    padding: 10px;
    border: none;
    background-color: white;
    font-weight: bold;
    cursor: pointer;
    }
    }
    
    
    .right{
    flex: 1;
    padding: 50px;
    display: flex;
    flex-direction: column;
    gap: 50px;
    justify-content: center;
    
     h1{
    color: #555;
    }
    
     form{
    display: flex;
    flex-direction: column;
    gap: 30px;
    
     input{
    border: none;
    border-bottom: 1px solid lightgray;
    padding: 20px 10px;
    }
    
     button{
    width: 50%;
    padding: 10px;
    border: none;
    background-color: #938eef;
    color: white;
    font-weight: bold;
    cursor: pointer;
    }
    }
    }
    }
    }
    

    