@import "../../Style.scss";


.rightbar{
  @include themify($themes){

    flex: 3;
    position: sticky;
    top: 70px;
    height: calc(100vh - 70px);
    overflow: scroll;
    background-color: themed("bgSoft");

    @include mobile{
       display: none;
    }

    @include tablet{
        display: none;
      }


&::-webkit-scrollbar{
    display: none;
}

.container{
    padding: 20px;

     

.item{
    -moz-box-shadow: 0px 2px 16px 2px rgba(0,0,0,0.09);
    -webkit-box-shadow: 0px 2px 16px 2px rgba(0,0,0,0.09);
    box-shadow: 0px 2px 16px 2px rgba(0,0,0,0.09);
    padding: 20px;
    margin-bottom: 20px;
    background-color: themed("bg");
    border-radius: 5px;

span{
    color: gray;
}

    .user{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0px;


.userinfo{
    display: flex;
    align-items: center;
    gap: 20px;
    position: relative;

img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    }

    .online{
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background-color: limegreen;
        position: absolute;
         top: 20px;
         left: 30px;
    }

    p{
        color: themed("textColorSoft")
    }
span{
    font-weight: 500px;
    color: themed("textColor")
    }
 }

   .buttons{
    display: flex;
    align-items: center;
    gap: 10px;
    button{
        border: none;
        padding: 5px;
        color: white;
        cursor: pointer;

        &:first-child{
            background-color: #5271ff;
        }
        &:last-child{
            background-color: red;
        }
    }
}
}
}
}
}
}