@import "../../Style.scss";

.stories{
    @include themify($themes){

    display: flex;
    gap: 10px;
    height: 250px;
    margin-bottom: 20px;
      background-color: themed("bg");
    color: themed("textColor");

    
@include mobile{
    height: 50px;
    justify-content: space-between;
 }
 @include tablet{
    height: 150px;
    gap: 20px;
  }

    .story{
        flex: 1;
        border-radius: 10px;
        overflow: hidden;
        position: relative;

        @include mobile{
            flex: none;
            width: 50px;
            height: 50px;
            border-radius: 50%;
          }

        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
        span{
            position: absolute;
            bottom: 10px;
            left: 10px;
            color: whitesmoke;
            text-align: center;
            font-weight: 800;
            
            
            @include mobile{
                display: none;
              }
            
        }
        button{
            position: absolute;
            bottom: 40px;
            left: 10px;
            color: white;
            background-color: #5271ff;
            border: none;
            border-radius: 50%;
            width: 30px;
            height: 30px;
            cursor: pointer;
            font-size: 30px;
            display: flex;
            justify-content: center;
            align-items: center;

            @include mobile{
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
              }
        }
    }
}
}